import {
    Component,
    h,
    Prop,
    Event,
    EventEmitter,
    Host,
    Element,
} from '@stencil/core';
import { classlist } from '../../utils/classlist';

const inputVariantsConfig = {
    variants: {
        disabled: {
            false: 'focus-within:ring-active-foreground/30 bg-white',
            true: 'bg-surface-1 !text-disabled pointer-events-none border-none',
        },
        readonly: {
            false: 'focus-within:ring-active-foreground/30',
            true: '!bg-surface-1 !text-dark cursor-text border-none',
        },
        invalid: {
            false: 'border-med focus-within:border-active-dark focus-within:ring-active-foreground/30',
            true: 'border-error-base focus-within:border-error-base focus-within:ring-destructive-foreground/30',
        },
    },
    defaultVariants: {
        disabled: false,
        readonly: false,
        invalid: false,
    },
};

@Component({
    tag: 'at-input-time',
    shadow: false,
})
export class AtInputTime {
    @Element() el!: HTMLElement;

    /** Current value in "HH:MM" or "HH:MM:SS" */
    @Prop({ mutable: true, reflect: true }) value?: string;

    /** Minimum time (e.g., "08:00") */
    @Prop() min?: string;

    /** Maximum time (e.g., "18:30") */
    @Prop() max?: string;

    /** Required flag */
    @Prop() required?: boolean;

    /** Step in seconds (e.g., 60 for minute granularity, 1 to allow seconds) */
    @Prop() step?: number = 60;

    /** Disabled flag */
    @Prop() disabled?: boolean;

    /** Readonly flag */
    @Prop() readonly?: boolean;

    /** Label text displayed above the input */
    @Prop() label?: string;

    /** Short description or validation hint displayed under the label */
    @Prop() hint_text?: string;

    /** Optional info icon tooltip text (used by at-form-label) */
    @Prop() info_text?: string;

    /** Error text displayed when invalid is set */
    @Prop() error_text?: string;

    /** Shows the error state styling and error text */
    @Prop() invalid?: boolean;

    /** Emits when value changes (normalized string or null) */
    @Event() atuiChange!: EventEmitter<string | null>;

    private inputId = `timepicker-${Math.random().toString(36).slice(2, 11)}`;

    private normalizeTimeString(raw?: string): string | undefined {
        if (!raw) return;
        const s = raw.trim();
        // Accept "HH:MM" or "HH:MM:SS" (24-hour)
        const m = /^([01]\d|2[0-3]):([0-5]\d)(?::([0-5]\d))?$/.exec(s);
        if (!m) return;
        const hh = m[1];
        const mm = m[2];
        const ss = m[3];
        return ss ? `${hh}:${mm}:${ss}` : `${hh}:${mm}`;
    }

    private onInput = (ev: Event) => {
        const input = ev.target as HTMLInputElement;
        const normalized = this.normalizeTimeString(input.value);
        this.value = normalized;
        this.invalid = !input.validity.valid;
        this.atuiChange.emit(normalized);
    };

    render() {
        const getClassname = classlist(
            'placeholder-text-light relative flex min-h-36 min-h-[36px] w-full flex-shrink flex-grow basis-0 gap-4 overflow-hidden rounded-md border border-solid overflow-ellipsis transition-colors duration-300 ease-in-out outline-none focus-within:ring-2 focus-within:outline-0',
            inputVariantsConfig,
        );

        const classname = getClassname({
            invalid: !!this.invalid,
            disabled: !!this.disabled,
            readonly: !!this.readonly,
        });

        return (
            <Host>
                <style>{`
                input[data-name="timepicker"]::-webkit-calendar-picker-indicator {
                    cursor: pointer;
                    position: absolute;
                    right: 8px;
                    top: 50%;
                    transform: translateY(-50%);
                }
                input[data-name="timepicker"] {
                    padding-right: 32px;
                    position: relative;
                }
                `}</style>
                <div class="flex flex-row">
                    <div class="flex-fill flex flex-col">
                        <div class="flex flex-col">
                            {(this.label ||
                                this.required ||
                                this.info_text) && (
                                <at-form-label
                                    label={this.label}
                                    for={this.inputId}
                                    required={!!this.required && !this.readonly}
                                    info_text={this.info_text}
                                />
                            )}

                            {this.hint_text && (
                                <span
                                    class="text-light mb-8 inline-block text-xs leading-tight"
                                    data-name="timepicker-hint"
                                >
                                    {this.hint_text}
                                </span>
                            )}

                            <div class={classname}>
                                <input
                                    type="time"
                                    id={this.inputId}
                                    name="timePicker"
                                    class="flex w-full cursor-pointer px-8 py-8 outline-0"
                                    readOnly={this.readonly ? true : undefined}
                                    disabled={this.disabled ? true : undefined}
                                    required={this.required}
                                    value={this.value ?? ''}
                                    min={this.min}
                                    max={this.max}
                                    step={this.step}
                                    data-name="timepicker"
                                    onInput={this.onInput}
                                />
                            </div>
                        </div>

                        {this.error_text && this.invalid && (
                            <span
                                class="text-error text-sm"
                                data-name="timepicker-error"
                            >
                                {this.error_text}
                            </span>
                        )}
                    </div>
                </div>
            </Host>
        );
    }
}
